body {
  background-color: #e0d6c4;
}

main {
  padding: 15px 30px;
}

a {
  text-decoration: none;
  display: inline-block;
}

.header-bar {
  height: 60px;
  background: linear-gradient(90deg, #2f0a0a 0%, brown 50%, #2f0a0a 100%);
  border-bottom: 1px solid #000;
  justify-content: center;
  align-items: center;
  display: flex;
  box-shadow: 0 5px 8px -3px rgba(0, 0, 0, .56);
}

.header-bar__input {
  width: 240px;
  height: 30px;
  margin-right: 10px;
}

.header-bar__input:focus {
  outline: none;
}

.header-bar__btn {
  height: 30px;
  background-color: orange;
  border-color: rgba(0, 0, 0, 0);
  border-radius: 3px;
  font-weight: 500;
  box-shadow: 3px 5px 10px rgba(0, 0, 0, .5);
}

.header-bar__btn:active {
  color: gold;
  background-color: #c4771a;
}

.gallery {
  flex-wrap: wrap;
  gap: 30px;
  display: flex;
}

.photo-card {
  flex-direction: column;
  flex-basis: calc(25% - 22.5px);
  display: flex;
}

.gallery {
  margin-bottom: 30px;
}

.photo-card__image {
  max-width: 100%;
  display: block;
}

.info {
  text-align: center;
  background-color: #e7dcdc;
  border: 1px solid #000;
  border-top: none;
  justify-content: space-between;
  padding: 0 10px;
  display: flex;
  box-shadow: 0 1px 1px rgba(0, 0, 0, .12), 0 4px 4px rgba(0, 0, 0, .06), 1px 4px 6px rgba(0, 0, 0, .16);
}

.info-item {
  flex-direction: column;
  display: flex;
}

.load-more {
  height: 30px;
  background-color: #1d7efc;
  border-color: rgba(0, 0, 0, 0);
  border-radius: 3px;
  align-items: center;
  margin: auto;
  display: none;
  box-shadow: 3px 5px 10px rgba(0, 0, 0, .5);
}

